import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import Swal from 'sweetalert2';
import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root',
})
export class GetsService {
  private url = environment.dbApi + `gets.php?type=`;
  //private url = './APIS/deportes/gets.php?type=';
  constructor(private http: HttpClient) {}

  GetData(type: string, order_by = '', param1 = null, param2 = null) {
    let params = '';
    if (order_by) {
      order_by = '&order_by=' + order_by;
    }
    if (param1) {
      params += '&params1=' + param1;
    }
    if (param2) {
      params += '&params2=' + param2;
    }
    let url_aux = this.url + type + order_by + params;

    return this.http.get(url_aux);
  }

  getHistory(params: HttpParams) {
    return this.http.get(environment.dbApi + 'search_history.php', { params });
  }
}
