import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class ToolsService {
  constructor() {}

  validEmail(value: string) {
    if (!value) {
      return false;
    } else {
      return (
        value.match(
          /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i
        ) != null
      );
    }
  }
  capitalizeAllWords(str: string): string {
    return str.toLowerCase().replace(/(^|\s)\S/g, (l) => l.toUpperCase());
  }

  cleanNumberInString(word: string): string {
    const numbers = '0123456789'.split('');
    for (const num of numbers) {
      while (word.indexOf(num) > -1) {
        word = word.replace(num, '');
      }
    }
    return word;
  }

  cleanUser(word: string): string {
    const chars = 'qwertyuiopñlkjhgfdsazxcvbnm0123456789';
    const aux = word.split('');
    for (const char of aux) {
      while (chars.indexOf(char) === -1 && word.indexOf(char) > -1) {
        word = word.replace(char, '');
      }
    }
    return word;
  }

  validatePhoneNumber(num: string, onlyNumer = false): string {
    const numbers = onlyNumer ? '0123456789' : '0123456789+-()/';
    const aux = num.split('');
    for (const char of aux) {
      while (numbers.indexOf(char) === -1 && num.indexOf(char) > -1) {
        num = num.replace(char, '');
      }
    }
    return num;
  }

  cleanCharInNumber(num: string): string {
    const numbers = '0123456789';
    const aux = num.split('');
    for (const char of aux) {
      while (numbers.indexOf(char) === -1 && num.indexOf(char) > -1) {
        num = num.replace(char, '');
      }
    }
    return num;
  }
  cleanCharInPrice(num: string, remove = false): string {
    const numbers = remove ? '0123456789' : '0123456789.';
    const aux = num.split('');
    for (const char of aux) {
      while (numbers.indexOf(char) === -1 && num.indexOf(char) > -1) {
        if (char === ',' && !remove) {
          num = num.replace(char, '.');
        } else {
          num = num.replace(char, '');
        }
      }
    }
    return num;
  }

  sortArray(key, arr: any[]) {
    arr.sort((a: any, b: any) => {
      if (a[key] < b[key]) {
        return -1;
      }
      return 0;
    });
  }

  formatDate(date: string, format = 'eeuu'): string {
    const aux = date.indexOf('/') > -1 ? date.split('/') : date.split('-');
    const hora = aux[2].split(' ')[1].substring(0, 5) + 'hs' ?? '';
    return format === 'eeuu'
      ? aux[2].split(' ')[0] + '/' + aux[1] + '/' + aux[0] + ' ' + hora
      : aux[0] + '/' + aux[1] + '/' + aux[2];
  }

  validateFormatDate(date, format = 'arg'): boolean {
    const aux = date.indexOf('/') ? date.split('/') : date.split('-');
    aux[0] = this.cleanCharInNumber(aux[0] || '');
    aux[1] = this.cleanCharInNumber(aux[1] || '');
    aux[2] = this.cleanCharInNumber(aux[2] || '');
    return aux[0].length === 2 && aux[1].length === 2 && aux[2].length === 4;
  }

  getNameListCountries(
    list: any[],
    item: any,
    key: 'countryName' | 'name' | 'toponymName'
  ) {
    const element = list.find((x) => x.geonameId === item);
    return element[key];
  }
}
