import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root',
})
export class CountriesService {
  private readonly API_URL = 'http://api.geonames.org/';
  private readonly API_URL_COUNTRIES = 'https://restcountries.com/v2/all';
  private readonly API_USERNAME = 'sapo89';

  constructor(private http: HttpClient) {}

  getCountries() {
    return this.http.get(
      `http://api.geonames.org/countryInfoJSON?username=sapo89`
    );
  }

  getProvinces(countryCode: any) {
    const url = `http://api.geonames.org/childrenJSON?geonameId=${countryCode}&username=${this.API_USERNAME}`;
    return this.http.get<any[]>(url);
  }

  getCities(provinceID: any) {
    return this.http.get(
      `http://api.geonames.org/childrenJSON?geonameId=${provinceID}&username=${this.API_USERNAME}`
    );
  }
}
